<template>
  <div class="content">
    <div class="left_plan">
      <div class="plan">
        <span class="p"></span>
      </div>
    </div>
    <div
      class="pt_thing"
      v-for="(i,index) in list"
      :key="index"
      :style="`animation-delay:${index*0.2}s;`"
    >
      <p class="time">{{i.time}}</p>
      <p class="project">项目：{{i.project}}</p>
      <p class="site">
        地址：
        <span v-if="!i.url">- -</span>
        <a :href="i.url" v-else target="_blank">{{i.url}}</a>
      </p>
      <p class="msg">{{i.msg}}</p>
      <div class="img_box" v-if="i.imgList && i.imgList.length">
        <div class="img" v-for="(j,index) in i.imgList" :key="index">
          <img :src="j.url" alt @click="toImgView" />
        </div>
      </div>
    </div>
    <img-view ref="imgView" :IMG_URL="imgUrl"></img-view>
  </div>
</template>

<script>
import imgView from "../common/ImgView";
export default {
  components: {
    "img-view": imgView
  },
  data() {
    return {
      imgUrl: "",
      list: [
        {
          time: "2020.3",
