          msg:
            " 该项目主要基于区块链的底层技术与互联网云服务，为长三角地区指定企业提供供应链可信信息登记、公示、确权、数据保全与存证、技术对接、共信机制孵化服务的中心机构。主要服务对象为围绕核心企业形成的供应链上的各类供应商、制造商、销售商、仓储物流商等中小微企业。",
          imgList: [
            {
              url: require("../../assets/img/sci_1.jpg")
            },
            {
              url: require("../../assets/img/sci_2.jpg")
            },
            {
              url: require("../../assets/img/sci_3.jpg")
            },
            {
              url: require("../../assets/img/sci_4.jpg")
            }
          ]
        },
        {
          time: "2019.6",
          project: "东金秀财（H5）",
          url: "https://xiucai-mobile.neafex.com",
          msg:
            " 该项目主要针对用户注册、登录、风险测评、签章授权、实名认证、新网支付等一系列金融资产认购流程，前期有PC端，后来转向移动端。截至目前（5.0版本）已经有两个大版本的更新。同时也针对后来的多渠道（中民普惠、中关网金、广州天象等）做了个性化定制，以及合规页面的分离开发。",
          imgList: [
            {
              url: require("../../assets/img/xiucai_1.jpg")
            },
            {
              url: require("../../assets/img/xiucai_2.jpg")
            },
            {
              url: require("../../assets/img/xiucai_3.jpg")
            },
            {
              url: require("../../assets/img/xiucai_4.jpg")
            },
            {
              url: require("../../assets/img/xiucai_5.jpg")
            }
          ]
        },
        {
          time: "2019.4",
          project: "BMS后台管理",
          url: "暂不开放",
          msg:
            " 该项目主要针对东金中心公告发布，东金秀财资产新增，加息券配置，地区额度配置等。同时添加了针对不同用户权限和角色的分配，功能模块由我和前端同事协同开发，后期我主要转向东金秀财H5版本开发。",
          imgList: [
            {
              url: require("../../assets/img/bms_1.jpg")
            },
            {
              url: require("../../assets/img/bms_2.jpg")
            },
            {
              url: require("../../assets/img/bms_3.jpg")
            }
          ]
        },
        {
          time: "2018.12",
          project: "东金中心",
          url: "https://www.neafex.com/",
          msg:
            " 该项目主要用于东金中心的公告发布与查询，有基础的用户登录与注册，以及后来加入的企业注册、挂牌方登录、不良债权、交易登记(有接入支付宝，微信支付功能)、个人中心等功能。方便用户在官网进行相关资产的查询与登记，",
          imgList: [
            {
              url: require("../../assets/img/neafex_1.jpg")
            },
            {
              url: require("../../assets/img/neafex_2.jpg")
            },
            {
              url: require("../../assets/img/neafex_3.jpg")
            },
            {
              url: require("../../assets/img/neafex_4.jpg")
            }
          ]
        },
        {
          time: "2018.02",
          project: "有好生鲜",
          msg:
            " 该项目主要是在该地区的生鲜购买系统，有基本的登陆注册功能,商品推荐,购物车,收获发货,评论点赞等功能。",
          imgList: [
            {
