            },
            {
              url: require("../../assets/img/2.1.png")
            },
            {
              url: require("../../assets/img/2.3.png")
            },
            {
              url: require("../../assets/img/2.4.png")
            }
          ]
        },
        {
          time: "2017.10",
          project: "Potting",
          msg:
            "该项目主要提供各种盆栽商品的销售,实现用户的登陆注册,同时包含购物车,问题反馈,支付等功能。",
          imgList: [
            {
              url: require("../../assets/img/3_1.jpg")
            },
            {
              url: require("../../assets/img/3_2.jpg")
            },
            {
              url: require("../../assets/img/3_3.jpg")
            }
          ]
        },
        {
          time: "2017.06",
          msg:
            "该项目主要为用户提供各大旅游景点,实现热门景点的定时更新,同时为用户提供旅行地点,旅行目的,旅行方式等方面的参考。",
          project: "去旅行",
          imgList: [
            {
              url: require("../../assets/img/1.1.jpg")
            },
            {
              url: require("../../assets/img/1.2.jpg")
            },
            {
