          imgList: [
            {
              url: require("../../assets/img/black_1.jpg")
            },
            {
              url: require("../../assets/img/black_2.jpg")
            },
            {
              url: require("../../assets/img/black_3.jpg")
            },
            {
              url: require("../../assets/img/black_4.jpg")
            }
          ]
        },
        {
          time: "- -",
          project: "自适应网页模板（个人）",
          url:'https://github.com/sunwenxu1997/adaptiveDifferentiation',
          msg:
            " 该项目主要基于 vue-cli2.0 + element-ui + mint-ui 实现自适应的网页模板项目,同时支持主题色更换。",
          imgList: [
            {
              url: require("../../assets/img/web_1.jpg")
            },
            {
              url: require("../../assets/img/web_2.jpg")
            },
            {
              url: require("../../assets/img/web_3.jpg")
            },
            {
              url: require("../../assets/img/web_4.jpg")
            },
            {
              url: require("../../assets/img/web_5.jpg")
            }
          ]
        }
      ]
    };
  },
  methods: {
    toImgView(e) {
      this.imgUrl = e.toElement.src;
      this.$refs.imgView.imgShow = true;
    }
  }
};
</script>

<style lang="scss" scoped>
.content {
  width: 100%;
  padding: 1rem;
  padding-left: 2rem;
}
.plan {
  width: 1px;
  height: 100%;
  background: #ccc;
  position: fixed;
  left: 1.5rem;
  top: 0;
  transform: scaleY(0);
  opacity: 0;
  animation: plan 0.5s ease both 1;
  .p {
    position: absolute;
    top: 0;
    left: 0;
    display: inline-block;
    width: 100%;
    height: 0%;
    background: var(--base);
    animation: planP 3s ease 1s both 1;
  }
}

.pt_thing {
  filter: drop-shadow(2px 2px 3px #ccc);
  margin: 1rem;
  background: white;
  padding: 0.5rem;
  position: relative;
  animation: textMoveLeft 0.5s ease both 1;
  &::before {
    content: "";
    width: 0px;
    height: 0px;
    border-right: 10px solid white !important;
    left: -20px;
    //   background: red;
    border: 10px solid transparent;
    position: absolute;
  }
  .time {
    font-size: 0.88rem;
    padding-bottom: 0.2rem;
    margin-bottom: 0.2rem;
    font-weight: bold;
  }
  .project {
    color: #5a5a5a;
    font-size: 0.75rem;
  }
  .site {
    color: #5a5a5a;
    font-size: 0.75rem;
    padding: 0.5rem 0;
    border-bottom: 1px solid #f1f1f1;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    a {
      color: #5a5a5a;
      font-weight: 100;
    }
  }
  .msg {
    font-weight: 100;
    font-size: 0.88rem;
    color: #5a5a5a;
    margin: 0.5rem 0;
  }
  .img_box {
    display: flex;
    flex-wrap: wrap;
    .img {
      border: 2px dashed #f1f1f1;
      width: 5rem;
      height: 5rem;
      overflow: hidden;
      margin-right: 0.5rem;
      margin-bottom: 0.5rem;
      text-align: center;
      &:active {
        opacity: 0.7;
      }
      img {
        display: inline-block;
        height: 100%;
      }
    }
  }
}
@keyframes plan {
  100% {
    opacity: 1;
    transform: scaleY(1);
  }
}
@keyframes planP {
  25% {
    height: 30%;
  }
  50% {
    height: 60%;
  }
  75% {
    height: 85%;
  }
  100% {
    height: 100%;
  }
}
@keyframes textMoveLeft {
  0% {
    opacity: 0;
    transform: translateX(2rem);
  }
  100% {
    opacity: 1;
    transform: translateX(0rem);
  }
}
</style>


